var ListItem, PouchDoc, uuid;

uuid = require("uuid");

PouchDoc = require("../PouchDb/pouch_doc.js");

ListItem = (function() {
  class ListItem extends PouchDoc {
    after_construct(model) {
      if (model != null) {
        this.depouchify(model);
      }
      this["list_reference"] = void 0;
      this["parent_reference"] = void 0; // null if it's a list
      this["lineage"] = void 0;
      this["depth"] = void 0;
      return this["list_items"] = [];
    }

    // update mirrored items
    refresh() {}

    // refresh?

    // attributes starting with _ are managed and need to be updated every time a change has been made

    // array version of list items
    default_value_for(key) {
      var output;
      output = null;
      switch (key) {
        case "is_expanded":
          output = {
            list: true,
            radar: true
          };
          break;
        case "is_ongoing":
        case "is_hidden":
          output = false;
          break;
        case "progress":
          output = {
            percent_complete: 0,
            color: "light-gray"
          };
          break;
        case "index":
        case "model_version":
          output = 0;
          break;
        case "labels":
        case "ancestry":
          output = [];
      }
      return output;
    }

    /*

      _     _                         _           
     | |__ (_) ___ _ __ __ _ _ __ ___| |__  _   _ 
     | '_ \| |/ _ \ '__/ _` | '__/ __| '_ \| | | |
     | | | | |  __/ | | (_| | | | (__| | | | |_| |
     |_| |_|_|\___|_|  \__,_|_|  \___|_| |_|\__, |
                                            |___/ 

     */
    child_ancestry() {
      return this.ancestry.clone().append(this._id);
    }

    /*

    _                 _  __       
     | |_ _ __ ___  ___(_)/ _|_   _ 
     | __| '__/ _ \/ _ \ | |_| | | |
     | |_| | |  __/  __/ |  _| |_| |
    \__|_|  \___|\___|_|_|  \__, |
                          |___/ 

     */
    // ancestryOverride is the parent's ancestry
    treeify(ancestryOverride) {
      var isOrphan, parentIndex, that;
      that = this;
      //console.log "ListItem.treeify"
      //console.log @
      // check  if orphan
      isOrphan = false;
      //console.log "ancsetry check"
      //console.log @ancestry
      if (this.ancestry.length > 0) {
        //console.log "ancestry last: #{@ancestry.last()}"
        //console.log "list items raw"
        //console.debug @list_reference.list_items_raw
        parentIndex = this.list_reference.list_items_raw.findIndex(function(parentCandidate) {
          return parentCandidate._id === that.ancestry.last();
        });
        isOrphan = parentIndex < 0;
      }
      //console.log "isOrphan: #{isOrphan}"
      //if isOrphan
      //  ancestryOverride = []
      this.list_items = this.list_reference.list_items_raw.filter(function(o) {
        return that._id === o.ancestry.last();
      });
      //console.log "this item's children"
      //console.log @list_items

      // fix

      //@list_items.forEach (o)->

      //if ancestryOverride?
      // fix ancestry
      //console.error "ancestryOverride: #{ancestryOverride}, before"
      //console.log JSON.stringify(o.ancestry)
      //console.debug o
      //o.ancestry = ancestryOverride #.append(that._id)

      // todo: re-enable when working
      // o.patch { id: o._id , ancestry: ancestryOverride.append(that._id) }

      //o.parent_reference = that
      //unless that.child_ancestry().isEqual(o.ancestry)
      //  console.error "item #{o.name} has bad ancestry.. should be [#{that.child_ancestry()}] is [#{o.ancestry.join(",")}]"
      //o.ancestry = that.child_ancestry()
      //  fixScb = (result)->
      //    console.warn "Fixed ancestry of [#{o._id}] #{o.name} in db"
      //  o.patch { ancestry: that.child_ancestry() }, fixScb
      return this.list_items.forEach(function(child) {
        //  if ancestryOverride?
        //    child.treeify(that.ancestry)
        //  else
        return child.treeify();
      });
    }

    /*
               _   
              | |  
     _ __   ___  ___| |_ 
    | '_ \ / _ \/ __| __|
    | | | |  __/\__ \ |_ 
    |_| |_|\___||___/\__|

     */
    nest(item) {
      var descendant, index, itemToAppend;
      console.error("ListItem.nest");
      console.log(item);
      console.log("@");
      console.log(this);
      if (item.ancestry.last() === this._id) {
        if (ListItem.nest != null) {
          itemToAppend = item;
        } else {
          itemToAppend = new ListItem(item);
        }
        return this.list_items.append(itemToAppend); // deeper
      } else {
        index = item.ancestry.findIndex(this._id) + 1;
        
        // find the child item in the current list
        descendant = this.list_items.find({
          _id: item.ancestry[index]
        });
        return descendant.nest(item);
      }
    }

    
    ////            _                 _                    _                 __            
    ////  _ __ ___ (_) __ _ _ __ __ _| |_ ___  __   ____ _| |_   _  ___     / _| ___  _ __ 
    //// | '_ ` _ \| |/ _` | '__/ _` | __/ _ \ \ \ / / _` | | | | |/ _ \   | |_ / _ \| '__|
    //// | | | | | | | (_| | | | (_| | ||  __/  \ V / (_| | | |_| |  __/   |  _| (_) | |   
    //// |_| |_| |_|_|\__, |_|  \__,_|\__\___|___\_/ \__,_|_|\__,_|\___|___|_|  \___/|_|   
    ////              |___/                 |_____|                   |_____|              

    // Override value for an attribute - use this to migrate old or invalid data
    migrate_value_for(model, key) {
      var output;
      output = model[key];
      if (key === "is_expanded") {
        if (Object.isBoolean(model[key])) {
          output = {
            radar: true,
            list: true
          };
        }
      }
      return output;
    }

    ////  _                          _                   _             
    //// | |__   ___  _   _ ___  ___| | _____  ___ _ __ (_)_ __   __ _ 
    //// | '_ \ / _ \| | | / __|/ _ \ |/ / _ \/ _ \ '_ \| | '_ \ / _` |
    //// | | | | (_) | |_| \__ \  __/   <  __/  __/ |_) | | | | | (_| |
    //// |_| |_|\___/ \__,_|___/\___|_|\_\___|\___| .__/|_|_| |_|\__, |
    ////                                          |_|            |___/ 

    // override if any attributes from the database need deleting, etc.
    housekeeping(model) {
      var deletionKeys;
      deletionKeys = ["is_expanded_radar", "lineage", "list_items", "description"];
      deletionKeys.forEach(function(key) {
        if (Object.has(model, key)) {
          return delete model[key];
        }
      });
      return model;
    }

    /*

       _                  
     ___| | ___  _ __   ___ 
    / __| |/ _ \| '_ \ / _ \
     | (__| | (_) | | | |  __/
    \___|_|\___/|_| |_|\___|

     */
    clone(options, scb) {
      var that;
      console.error("CLONE OPTIONs");
      console.error(options);
      that = this;
      return db.find({
        selector: {
          $or: [
            {
              ancestry: {
                $all: [that._id]
              }
            },
            {
              _id: that._id
            }
          ]
        }
      }).then(function(result) {
        var clonedDocs, newKeys;
        console.error("clone queue");
        console.log(result.docs);
        newKeys = {};
        clonedDocs = [];
        result.docs.sortBy(function(doc) {
          return doc.ancestry.length;
        });
        result.docs.forEach(function(doc) {
          var newDoc;
          newKeys[doc._id] = uuid();
          newDoc = new ListItem(doc);
          if (doc._id === that._id) {
            newDoc.name = `${newDoc.name} copy`;
            // new index
            if (options != null) {
              if (options.index != null) {
                newDoc.index = options.index;
              } else {
                options.index = doc.index + Number.random(100, 200);
              }
              if (options.list_id != null) {
                newDoc.ancestry = [];
              }
            }
          }
          if (options) {
            if (options.list_id != null) {
              newDoc.list_id = options.list_id;
              // trim the ancestry before rekeying
              newDoc.ancestry = newDoc.ancestry.from(newDoc.ancestry.findIndex(that._id)); // dupe
            }
          }
          console.error(`newDoc._id = newKeys[doc._id] (${newKeys[doc._id]})`);
          newDoc._id = newKeys[doc._id];
          console.error(`original ancestry: ${JSON.stringify(doc.ancestry)}`);
          newDoc.ancestry = doc.ancestry.map(function(ancestor) {
            if (Object.has(newKeys, ancestor)) {
              return newKeys[ancestor];
            } else {
              return ancestor;
            }
          });
          console.error(`new ancestry:  ${JSON.stringify(newDoc.ancestry)}`);
          return clonedDocs.append(newDoc.pouchify());
        });
        console.debug(newKeys);
        console.error("old docs");
        console.debug(result.docs);
        console.error("new docs");
        console.debug(clonedDocs);
        console.log("bulk insert");
        // todo: bulk insert docs
        return db.bulkDocs(clonedDocs).then(function(result) {
          console.log("bulk docs result");
          console.debug(result);
          console.log("scb?");
          console.log(scb);
          if (scb != null) {
            return scb(result);
          }
        }).catch(function(err) {
          console.log("bulk insert error");
          return console.log(err);
        }).finally(function() {
          console.log("finally");
          return that.sync();
        });
      });
    }

    
    // get children irrespective of level
    get_descendants(scb, ecb) {
      var that;
      that = this;
      return db.find({
        selector: {
          ancestry: {
            $all: [that._id]
          }
        }
      }).then(function(result) {
        if (scb != null) {
          return scb(result.docs);
        }
      });
    }

    save(cb) {
      var item, that;
      that = this;
      item = this.pouchify();
      return db.put(item).then(function(result) {
        if (cb != null) {
          return cb(result);
        }
      }).finally(function() {
        return that.sync();
      });
    }

    delete(cb) {
      var that;
      that = this;
      // remove this and its children
      return db.find({
        selector: {
          $or: [
            {
              ancestry: {
                $all: [that._id]
              }
            },
            {
              _id: that._id
            }
          ]
        }
      }).then(function(result) {
        console.error("Deletion queue");
        console.log(result.docs);
        return db.bulkDocs(result.docs.map(function(doc) {
          doc["_deleted"] = true;
          return doc;
        })).then(function(result) {
          console.error("Lists deleted");
          if (cb != null) {
            return cb(result);
          }
        });
      }).finally(function() {
        return that.sync();
      });
    }

  };

  ListItem.prototype.model = "List Item";

  ListItem.prototype.doctype = "ListItem";

  ListItem.prototype.attribute_whitelist = ["list_id", "model_version", "name", "description", "progress", "index", "ancestry", "is_hidden", "is_expanded", "is_ongoing", "due_date", "labels", "user_defined_data", "created_at", "updated_at"];

  // treeify support
  ListItem.prototype.list_items = [];

  return ListItem;

}).call(this);

module.exports = ListItem;
