<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    Indent,
    InlineInput 
    SearchHighlighter
  } from "../../Braintapper"


  import { 
    ProgressPopover,  
    DueDatePopover, 
    ListItemMenu,
    ListItemRow
    ListHoverableContainer
    
    } from "../../Application"


  




  export keywords = ""
  export list = undefined
  export listId = undefined



  goToList = ()->
    location.href="#/lists/#{listId}"


  toggleExpanded = (item)->
    console.log "toggle Expanded"
    
    item.is_expanded.radar = !item.is_expanded.radar
    dispatch "update", {_id: item._id, is_expanded: {radar: item.is_expanded.radar, list: item.is_expanded.list} }

  

  updateListItem = (e)->
    console.log "Radar._ListItems menu"
    console.log e.detail
    if listId?
      console.log "top event listid: #{listId}"
      e.detail["list_id"] = listId
    dispatch "update", e.detail



  destroyDispatcher = (e)->
    dispatch "destroy", e.detail
    showDeleteConfirmation = false


</script>


{#each list.list_items as listItem, index}
  
  <ListHoverableContainer>
    <div type="search-item">
      <ListItemRow item={listItem} context="search"  classes="search-item" on:update={updateListItem}/>

    </div>
    <div slot="menu">
      <ListItemMenu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher} context="radar"/>
    </div>
  </ListHoverableContainer>

    <!-- children -->
    {#if (listItem.list_items || []).length > 0}
      
        <svelte:self list={listItem} {listId} on:update={updateListItem} on:destroy={destroyDispatcher} {keywords}/>
      
    {/if}
  
{/each}


<style lang="sass">

  [type="search-item"]

    background: transparent

    [item-interior]
      width: calc(100%)
      max-width: calc(100%)

    [collapsible], [menu], [progress] // set cell wdiths
      width: 32px


    [menu]
      text-align: center

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    



    [collapsible]
      line-height: 24px
      svg, svg.menu
        display: inline


    &:hover

      [handle], [collapsible], [draggable-item-menu]
        svg, svg.menu
          display: inline



      [collapsible]
        button[icon]
          padding: 0px !important
        svg
          path.fill
            fill: var(--light-gray)
        &:hover
          svg
            path
              fill: var(--blue)


</style>
