<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()



  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    Indent,
    InlineInput 
  } from "../../Braintapper"


  import { 
    ListItem, 
    ListItemRow
    ListItemMenu,  
    ListDraggableContainer,
    ListDroppableDivider,
    ProgressPopover, 
    DueDatePopover 
    } from "../../Application"



  
  export list = []
  export dragState = undefined
  

  message = ""

  style = ":hover {font-weight: bold}"

  goToItem = (childItem)->
    location.href="#/lists/#{childItem._id}"

  #//                  _       _       _     _     _   ___ _                 
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|___|\__\___|_| |_| |_|
  #//       |_|                                                              

  updateListItem = (e)->
    console.error "_ListItems.svelte Update List Item"
    console.log e.detail
    dispatch "update", e.detail

  cloneListItem = (e)->
    dispatch "clone", e.detail

  cloneListItemTo = (e)->
    dispatch "cloneTo", e.detail

  destroyDispatcher = (e)->
    dispatch "destroy", e.detail
    showDeleteConfirmation = false

  # drag and drop

  dividerDropDispatcher = (e)->
    # 
    dispatch "drop", e.detail


  ###
    navigator.clipboard.readText().then (o)->
      add o
  ###

  #//                  _   ___       _        
  #//  _ __   ___  ___| |_|_ _|_ __ | |_ ___  
  #// | '_ \ / _ \/ __| __|| || '_ \| __/ _ \ 
  #// | | | |  __/\__ \ |_ | || | | | || (_) |
  #// |_| |_|\___||___/\__|___|_| |_|\__\___/ 
  #//                                         

  nestNewInto = (e)->

    if e.detail.list_items.length == 0
      newIndex = 0
    else
      newIndex = e.detail.list_items.last().index + 1000

    newStub = {
      index: newIndex
      ancestry: e.detail.ancestry.clone().append(e.detail._id)
      name: "New item created at #{Date.create().format('{HH}:{mm}:{ss}')}"
    }

    addDispatcher { detail: newStub }


  #//                  _   ____  _                 _       _               
  #//  _ __   ___  ___| |_|  _ \(_)___ _ __   __ _| |_ ___| |__   ___ _ __ 
  #// | '_ \ / _ \/ __| __| | | | / __| '_ \ / _` | __/ __| '_ \ / _ \ '__|
  #// | | | |  __/\__ \ |_| |_| | \__ \ |_) | (_| | || (__| | | |  __/ |   
  #// |_| |_|\___||___/\__|____/|_|___/ .__/ \__,_|\__\___|_| |_|\___|_|   
  #//                                 |_|                                  

  nestDispatcher = (e)->
    dispatch "nest", e.detail


  #//            _     _ ____       _               
  #//   __ _  __| | __| | __ )  ___| | _____      __
  #//  / _` |/ _` |/ _` |  _ \ / _ \ |/ _ \ \ /\ / /
  #// | (_| | (_| | (_| | |_) |  __/ | (_) \ V  V / 
  #//  \__,_|\__,_|\__,_|____/ \___|_|\___/ \_/\_/  
  #//                                               

  addBelow = (e)->

    itemIndex = list.list_items.findIndex (item)->
      return item._id == e.detail._id
    
    if itemIndex == (list.list_items.length - 1)
      newIndex = list.list_items[itemIndex].index + 1000
    else
      newIndex = [list.list_items[itemIndex].index, list.list_items[itemIndex + 1].index].median()

    newStub = {
      index: newIndex
      ancestry: e.detail.ancestry
      name: "New item created at #{Date.create().format('{HH}:{mm}:{ss}')}"
    }

    addDispatcher { detail: newStub }



  cloneListItem = (e)->
    dispatch "clone", e.detail


  cloneListItemTo = (e)->
    dispatch "cloneTo", e.detail

  addDispatcher = (e)->
    console.log "addDispatcher"
    dispatch "add", e.detail

  

  onDrag = (e)->
    dispatch "dragging", true

  onDragStop = (e)->
    #console.log e
    #console.log "dragStop"
    dispatch "dragStop", true



  toggleExpanded = (item)->
    # force fix for old data
    unless item.is_expanded.list?
      item.is_expanded["list"] = false

    unless item.is_expanded.radar?
      item.is_expanded["radar"] = false


    item.is_expanded.list = !item.is_expanded.list
    dispatch "update", {_id: item._id, is_expanded: { list: item.is_expanded.list, radar: item.is_expanded.radar } }



  getAncestry = (list)->
    if list.doctype == "List"
      []
    else
      (list.ancestry || []).clone().append(list._id)  

  minIndex = (list)->
    if Object.has(list,'list_items')
      
      min = list.list_items.min (o)->
        return o.index
      

      if min?
        min.index - 1000
      else
        - 1000

    else
      - 1000


  maxIndex = (list)->
    if Object.has(list,'list_items')
      
      max = list.list_items.max (o)->
        
        return o.index
      if max?
        max.index + 1000
      else
        1000

    else
      1000

  getIntermediateIndex = (currentListIndex)->
    if currentListIndex == (list.list_items.length - 1)  # last
      list.list_items.last().index + 1000
    else
      median = [ list.list_items[currentListIndex].index, list.list_items[currentListIndex + 1].index ].median()
      


  refreshed = (o)->
    #console.error "--------------------------refreshed listitems"
    #console.log o
    return o.sortBy("index")


  firstChildIndex = (list)->
    if list?
      if list.length == 0
        -1000
      else
        list.first().index - 1000      
    else
     -1000

  `$: firstIndex = minIndex(list)`
  `$: lastIndex = maxIndex(list)`
  `$: levelAncestry = getAncestry(list)`
  `$: listItems =  refreshed(list.list_items)`
  

</script>
{#if (list.ancestry)} <!-- first child on a sublist -->
  <ListDroppableDivider item={{ ancestry: (list.ancestry || []).clone().append(list._id), index: minIndex(list), nest: true, pre: true} } indentation={list.ancestry.length + 1} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} firstItem={true} on:add={addDispatcher} index={-1} bind:dragState={dragState}/>
{/if}

{#if listItems}
{#each listItems as listItem, index}

  <ListDraggableContainer item={listItem} on:nest={nestDispatcher} on:update={updateListItem} on:dragging={onDrag} on:dragStop={onDragStop} >
      <div type="list-item">
        <ListItemRow item={listItem} context="list" on:update={updateListItem}/>
      </div>
      <div slot="menu">
        <ListItemMenu item={listItem} on:update={updateListItem} on:clone={cloneListItem} on:cloneTo={cloneListItemTo} on:destroy={destroyDispatcher} on:add={addBelow} on:nest={nestNewInto}/>
      </div>
    
  </ListDraggableContainer>
  {#if false && (listItem.list_items.length == 0) || (!listItem.is_expanded.list)}
  <!-- first child -->
    
    <ListDroppableDivider item={{ ancestry: listItem.ancestry.clone().append(listItem._id),  index: firstChildIndex(listItem.list_items), nest: true}} indentation={listItem.ancestry.length + 1} on:drop={dividerDropDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
    <div style="height: 4px;"/>
    
  {/if}
  <!-- children -->
  {#if listItem.is_expanded.list }
    <svelte:self list={listItem}  bind:dragState={dragState} on:update={updateListItem} on:drop={dividerDropDispatcher} on:clone={cloneListItem} on:cloneTo={cloneListItemTo} on:nest={nestDispatcher} on:destroy={destroyDispatcher} on:add={addDispatcher} on:dragging={onDrag} on:dragStop={onDragStop}/>
  {/if}
  <!-- same level trailing -->
  {#if true }
    <ListDroppableDivider item={{ ancestry: levelAncestry, index: getIntermediateIndex(index), nest: true, post: true}} indentation={listItem.ancestry.length} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher}  bind:dragState={dragState}/>
    <div style="height: 4px;"/>
  {/if}


{/each}
{/if}

<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%

    [main]
      width: 100px // this is a dummy value to make the ellipsis work // todo: check if this is still necessary now that there is FormatEllipsis
    &:hover
      [present]
        display: flex

</style>
