<script lang="coffeescript">


  import { createEventDispatcher } from "svelte"
  
  import {
    FlexColumn, 
    FlexColumnCell, 
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"


  import { 
    debug, 
    ButtonIcon,
    DateCalendar, 

    FormatRelativeDate,
    IconSvg
  } from "../../../Braintapper"

  dispatch = createEventDispatcher()

  export value = undefined
  export isShowing = undefined




  clear = ()->
    console.error "_DueDate clear"
    dispatch "tippy-event", { event: "clear", value: "" }

  setDate = (e)->
    console.error "_DueDate setDate #{JSON.stringify(e.detail)}"
    event = { event: "set", value: e.detail }
    console.log event
    dispatch "tippy-event", event



</script>




<FlexColumn>
  <FlexColumnCell>
    <FlexRow>
      <FlexRowCell flex="flex" style="padding: 4px 0px 4px 8px">
        <span relative-date>
        
          {#if value == null}
            No date assigned
          {:else}
            <FormatRelativeDate {value}/>
          {/if}
        </span>
      </FlexRowCell>
      <FlexRowCell>
        <ButtonIcon on:click={clear}>
          <IconSvg icon="clear"/>
        </ButtonIcon>
      </FlexRowCell>
    </FlexRow>
  </FlexColumnCell>
  <FlexColumnCell>
    <div calendar>
      <DateCalendar {value} bind:isShowing={isShowing} on:change={setDate}/>
    </div>
  </FlexColumnCell>
</FlexColumn>



<style lang="sass">
  [relative-date]
    font-size: var(--font-s)
</style>