<script lang="coffeescript">

  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  import { IsDragging, CurrentlyDragging } from "./stores.js"

  import {
    FlexRow
    FlexRowCell
  } from "braintapper-svelte-flex"

  import { 
    debug
    IconSvg
  } from "../../../Braintapper"
  
  

  export item = undefined
  
 
  # locally tracked store values

  currentlyDragging = undefined
  isDragging = false



  onMount ()->
    IsDragging.subscribe  (val)->
      isDragging = val
    CurrentlyDragging.subscribe  (val)->
      currentlyDragging = val          


  
  draggable = false
  isOver = false
  isInvalid = false

  

  start = (e)->
    
    isInvalid = false
    #console.log "START"
    # console.log item
    CurrentlyDragging.set item  

    e.dataTransfer.setData("text/plain", JSON.stringify(item.safe_model()))
    e.dataTransfer.setData("listid:#{listId}", JSON.stringify(item.safe_model()))
    isDragging.set true
    dispatch "dragging", true

  end = (e)-> # stop dragging
    CurrentlyDragging.set undefined
    isDragging.set false
    isInvalid = false
    dispatch "dragStop", true
    

  enter = (e)->
    # Enter is not a useful event for determining drop validity
    # Placeholder for future functionality


  over = (e)->
    # for invalid state, over is much more reliable than enter
    e.preventDefault()
    isOver = true
    isInvalid = item.ancestry.includes(currentlyDragging.id) || (item.id == currentlyDragging.id) # || (item.id == currentlyDragging.ancestry.last())
    


  leave = (e)->
    console.log "leave"
    draggable = false
    isOver = false
    isInvalid = false

  drop = (e)->
    
    isOver = false
    isInvalid = false
    draggable = false
    e.preventDefault()
    dispatch "dragStop", true
    # todo: consider dropping the e.dataTransfer
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))
    #console.log "postparse"
    dropped = true

    console.log currentlyDragging # still exists, not wiped untile END

    if dropped

      if item.ancestry.includes(currentlyDragging.id) || (item.id == currentlyDragging.id) || (item.id == currentlyDragging.ancestry.last())
        console.error "CAN'T DROP ON ANCESTOR OR SELF"
        # todo: toast this
      else
        console.info "good to go" 
        dispatch "nest",
          dragItem: dragItem
          dragIndex: dragItem.index
          dropItem: item
          dropIndex: item.index




  mouseup = ()->
    # console.error "MOUSEUP"
    draggable = false
    isDragging.set false
    isInvalid = false
    

  mousedown = ()->
    # isDragging.set true
    draggable = true



  

</script>


<div
  item-draggable
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragenter={enter}
  on:dragover={over}
  on:dragstart={start}
  on:dragend={end}
  on:dragleave={leave}
  data-id={item._id}
  data-index={item.index}
  data-name={item.name}
  data-ancestry={JSON.stringify(item.ancestry)}
>
  <FlexRow>
    <FlexRowCell flex="initial" >
      <div on:mouseup={mouseup} on:mousedown={mousedown} item-draggable-handle is-dragging={isDragging} visible-on-hover>
        <IconSvg classes="handle" icon="handle"/>
      </div>      
    </FlexRowCell>
    <FlexRowCell flex="true">
      <slot/>
    </FlexRowCell>
    <FlexRowCell flex="initial">
      <div item-draggable-menu  visible-on-hover>
        <slot name="menu"/>
      </div>
    </FlexRowCell>
  </FlexRow>



</div>


<style global lang="sass">

  // 24+8px = 32 for icon cols

  

  [item-draggable]

    --item-draggable-icon-width: 24px 
    
    padding: 4px 0px

    border-radius: 3px
    // 3 icons - grabber, expander, menu
    width: calc(100% + calc(3 * var(--item-draggable-icon-width)))
    max-width: calc(100% + calc(3 * var(--item-draggable-icon-width)))
    // 2 icons grabber, expander
    margin-left: calc(var(--item-draggable-icon-width) * -2)

    [item-draggable-handle], [item-draggable-menu] 
      width: var(--item-draggable-icon-width)



    [item-draggable-handle]
      width: 24px
      height: 24px
      
      &:hover
        cursor: grab

      &[is-dragging="true"]
        cursor: grabbing !important

    [item-draggable-handle], [item-draggable-menu] 
      svg 
        path
          fill: transparent




    [item-draggable-menu] 
      //padding-left: 8px


    [visible-on-hover]
      & > svg
        path
          fill: transparent
    &:hover
      background: var(--list-row-highlight)
      [visible-on-hover]
        svg
          path
            fill: var(--light-gray)
    
    &.over
      background: var(--light-blue) !important
      

    &.invalid, &.invalid.over
      background: var(--light-gray) !important

</style>
