<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()



  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    Indent,
    InlineInput 
  } from "../../../Braintapper"

  import { 
    ProgressPopover
    DueDatePopover 
  } from "../../../Application"


  export item = undefined
  export context = "current"
  export classes = undefined
  



  toggleExpanded = ()->
    item.is_expanded[context] = !item.is_expanded[context]
    dispatch "update", {_id: item._id, is_expanded: item.is_expanded }

  updateItem = (e)->
    dispatch "update", e.detail

  goToList = ()->
    location.href="#/lists/#{item._id}"



</script>

<FlexRow classes>
  <FlexRowCell flex="initial">
    <div progress>
      <ProgressPopover {item} value={item.progress} on:update={updateItem} on:open on:close idAttribute="_id"/>
    </div>
    
  </FlexRowCell>

  <FlexRowCell flex>
    <InlineInput item={item} on:update={updateItem} on:clicked={goToList}>
      <FormatEllipsis>
        <a href="#/lists/{item._id}" on:click|preventDefault><em>{item.name || "No Names"}</em></a>
      </FormatEllipsis>      
    </InlineInput>
  </FlexRowCell>

  <FlexRowCell flex="initial">
    <div layout="row">
      <div flex="initial" present={item.due_date != null}>
        <DueDatePopover {item} value={item.due_date} on:update={updateItem} idAttribute="id" on:open on:close/>
      </div>

    </div>
  </FlexRowCell>

</FlexRow>

<style lang="sass">
  [progress]
    padding-right: 8px
  [collapsible]
    line-height: 24px
    svg, svg.menu
      display: inline


</style>