<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    Indent,
    InlineInput 
  } from "../../../Braintapper"

  import { 
    ProgressPopover
    DueDatePopover 
  } from "../../../Application"


  export item = undefined
  export context = "list"
  export classes = undefined
  

  toggleExpanded = ()->
    console.log "toggle Expanded #{context}"
    item.is_expanded[context] = !item.is_expanded[context]
    dispatch "update", {_id: item._id, is_expanded: item.is_expanded }

  updateItem = (e)->
    
    dispatch "update", e.detail

  updateDueDate = (e)->
    console.log "updateDueDate : #{JSON.stringify(e.detail)}"
    item.due_date = e.detail
    item = item # force refresh
    console.log "ITEMROW predispatch"
    updateItem {
      detail:
        _id: item._id
        due_date: e.detail
    }

  checkDate = (val)->
    if val?
      if val.due_date?
        Date.create(val.due_date)
      else
        null
    else
      null



  `$: dueDate = checkDate(item)`


</script>


<FlexRow {classes}>
    <FlexRowCell flex="initial" style="width: {(item.ancestry || []).length * 32}px">
      <Indent level={(item.ancestry || []).length} width={32}/>
    </FlexRowCell>
    {#if context != "search"}
    <FlexRowCell classes="collapsible">
      <div collapsible>
        {#if (item.list_items || []).length > 0}
          <ButtonIcon on:click={toggleExpanded()}>
            <IconToggleSvg value={item.is_expanded[context]} whenTrue="expanded" whenFalse="collapsed"/>
          </ButtonIcon>
          
        {:else}
          <IconSvg/>
        {/if}
      </div>
    </FlexRowCell>  
    {/if}  
    <FlexRowCell flex>
      <FlexRow>
        <FlexRowCell flex="initial">
          <div progress>
            <ProgressPopover item={item} value={item.progress} on:update={updateItem} on:open on:close/>
          </div>
        </FlexRowCell>
        <FlexRowCell flex style="width: 100px">
          
            {#if context == "list"}

              <InlineInput {item} on:update={updateItem}>
                <!-- dry this up -->
                {#if item.is_hidden}
                  <FormatEllipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</FormatEllipsis>
                {:else}
                  <FormatEllipsis>
                    {#if item.is_ongoing}
                      <span><IconSvg icon="ongoing" size="12"/></span>
                    {/if}                  
                    {item.name || "No Name"}
                  </FormatEllipsis>

                {/if}
              </InlineInput>

            {:else}

              {#if item.is_hidden}
                <FormatEllipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</FormatEllipsis>
              {:else}
                <FormatEllipsis>
                  {#if item.is_ongoing}
                    <span><IconSvg icon="ongoing" size="12"/></span>
                    &nbsp;
                  {/if}                
                  {item.name || "No Name"}
                </FormatEllipsis>
              {/if}            
            {/if}          
          
        </FlexRowCell>
        <FlexRowCell flex="initial">
          <div visible-on-hover>
            <DueDatePopover value={dueDate} on:update={updateDueDate} on:open on:close/>
          </div>
        </FlexRowCell>
      
      </FlexRow>
    </FlexRowCell>
</FlexRow>

<style lang="sass">
  [progress]
    padding-right: 8px
  [collapsible]
    line-height: 24px
    svg, svg.menu
      display: inline


</style>