<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()



  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    Indent,
    InlineInput 
  } from "../../../Braintapper"

  import { 
    ProgressPopover
    DueDatePopover 
  } from "../.."


  export list = undefined
  export context = "radar"
  export classes = undefined
  



  toggleExpanded = ()->
    item.is_expanded[context] = !item.is_expanded[context]
    dispatch "update", {_id: item._id, is_expanded: item.is_expanded }

  updateList = (e)->
    console.error "TitleRow.svelte updateList"
    console.debug e.detail

    dispatch "update", e.detail

  updateDueDate = (e)->
    console.log "updateDueDate!! : #{JSON.stringify(e.detail)}"
    list.due_date = e.detail
    list = list # force refresh
    console.log "ITEMROW predispatch"
    updateList {
      detail:
        _id: list._id
        due_date: e.detail
    }


  checkDate = (val)->
    console.log "check date"
    if val?
      if val.due_date?
        Date.create(val.due_date)
      else
        null
    else
      null

</script>

<FlexRow classes="radar-list" style="line-height: 36px">


  <FlexRowCell flex="initial" style="padding-right: 8px; padding-top: 3px">
    <ProgressPopover item={list} value={list.progress} on:update={updateList} on:open on:close/>
  </FlexRowCell>

  <FlexRowCell flex>
    <FormatEllipsis>
      <h3><a href="#/lists/{list._id}">{list.name || "No Name"}</a></h3>
    </FormatEllipsis>
      

  </FlexRowCell>

  <FlexRowCell flex="initial">
    <div layout="row">
      <div flex="initial" present={list.due_date != null} visible-on-hover>
        <DueDatePopover value={checkDate(list)} on:update={updateDueDate} on:open on:close/>
      </div>
    </div>
  </FlexRowCell>

  
</FlexRow>


<style lang="sass">
  [progress]
    padding-right: 8px
  [collapsible]
    line-height: 24px
    svg, svg.menu
      display: inline


</style>