<script lang="coffeescript">

  import { createEventDispatcher } from 'svelte';

  dispatch = createEventDispatcher()

  import { ModalContainer, Button } from "../../../Braintapper"

  import { ListPickerMenu } from "../../../Application"



  export entity = "item"

  selectedItem = undefined

  select = (e)->

    console.log "CLone to: #{e.detail._id} (#{e.detail.name})"
    selectedItem = e.detail
    # dispatch "selectList", "listid goes here"
    
  cancel = ()->
    dispatch "cancel"


  clone = ()->
    console.log "CLONE"
    console.log selectedItem
    dispatch "cloneTo", selectedItem



</script>


<ModalContainer>
  <div slot="header">
    Clone To Another List
  </div>
  <div slot="footer">
    <div a="r">
      <Button on:click="{clone}" state="primary" >Clone</Button>
      <Button on:click="{cancel}" >Cancel</Button>
    </div>
  </div>
  <div>
    
    <ListPickerMenu on:select={select}/>

  </div>
</ModalContainer>

