<script lang="coffeescript">

  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  
  import {
    IconProgressCircle
    TippyPopover
  } from "../../../Braintapper"
  import { debug
    FormatRelativeDate
    IconSvg 
  } from "../../../Braintapper"


  import DueDate from "./_DueDate.svelte"


  tippyObject = undefined
  component = undefined
  isShowing = false

  export value = null
  export style = undefined
  export item = undefined


  isShowing = false

  #onMount ()->
  #  console.error "due date mount, value: #{JSON.stringify(value)}"



  tippyComponent = undefined
  tippyObject = undefined

  clear = ()->
    tippyObject.hide()
    console.error "DueDate clear"
    dispatch "close"
    dispatch "update", { value: null }
    tippyComponent.$set { value: null }  

  setDate = (e)->
    console.log "DueDate setDate"
    console.log "----- new value"
    console.log e.detail
    if e.detail?
      tippyComponent.$set { value: Date.create(e.detail) }
    else
      tippyComponent.$set { value: e.detail }
    value = e.detail
    dispatch "update", e.detail
    tippyObject.hide()  

  show = ()->
    isShowing = true
    tippyComponent.$set { isShowing: isShowing }

  hide = ()->
    isShowing = false
    tippyComponent.$set { isShowing: isShowing }

  initPopover = ()->
    tippyComponent.$set { value: value, isShowing: isShowing }

  updatePopover = ()->
    tippyComponent.$set { value: value }


    
</script>

<TippyPopover 
  template={DueDate}
  on:set={setDate}
  on:clear={clear}
  on:show={show}
  on:hide={hide}
  on:create={initPopover}
  bind:tippyComponent={tippyComponent}
  bind:tippyObject={tippyObject}
  theme="datepicker">

  <div show-on-hover is-showing={isShowing} {style}>
      {#if value == null}
        <IconSvg icon="calendar"/>

      {:else}
        <a duedate><em>
          <FormatRelativeDate {value}/>
        </em></a>

      {/if}

  </div>
</TippyPopover>
<style global lang="sass">
  .tippy-box
    &[data-theme="datepicker"]
      width: 240px

</style>
