<script lang="coffeescript">




  import FileSaver from "file-saver"
  import md5 from "md5"


  # Data Items

  import { 
    debug,
    Button,
    FileInput
    InlineInput,
    InlinePassword,
    Checkbox 
  } from "../../Braintapper"

  import { 
    List
    ListItem
    PageContent
    PageHeader 
  } from "../../Application"



  


  import {createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()


  export files = undefined

  export fileObj =  {}

  export jsonFile = undefined



  `$: if (files) { fileObj = files }`


  fixIdMismatch = ()->
    console.error "fixIdMismatch"
    selector = 
      $or: [
        { doctype: "List" }
        { doctype: "ListItem" }
      ]

    db.find { selector: selector }
    .then (result)->
      mismatched = result.docs.filter (o)->
        return o.id != o._id

      console.error "MISMATCHED"
      console.log mismatched
      mismatched = mismatched.map (o)->
        o.id = o._id
        return o
      
      console.log "READY TO FIX"
      db.bulkDocs mismatched
      .then (response)->
        console.error "bulk update mismatches"
        console.debug response

        




  fixOrphans = ()->
    selector = 
      $or: [
        { doctype: "List" }
        { doctype: "ListItem" }
      ]

    db.find { selector: selector }
    .then (result)->

      console.log "result docs"
      console.debug result.docs
      foundDocs = result.docs.groupBy("doctype")
      console.error "foundDocs"
      console.log foundDocs
      foundDocs["List"] = foundDocs["List"].groupBy("id")
      foundDocs["ListItem"] = foundDocs["ListItem"].groupBy("list_id")

      projectIds = Object.keys(foundDocs["List"])
      listItemProjectIds = Object.keys(foundDocs["ListItem"])

      console.error "projectIds"
      console.debug projectIds

      console.error "listItemProjectIds"
      console.debug listItemProjectIds

      orphans = []
      listItemProjectIds.forEach (listId)->
        if foundDocs["List"][listId]?
          console.log "#{listId} found, should be valid, skpping"
        else
          console.log "#{listId} is orphaned"
          orphans.append listId

      console.error "ORPHAN list ids"
      console.debug orphans


      newOrphanList = new List
      newOrphanList.name = "New Orphan List"
      newOrphanList = newOrphanList.pouchify()

      newOrphanId = newOrphanList_id

      bulkDocs = []

      orphans.forEach (orphanListId)->
        console.log "orphanLIstId #{orphanListId}"
        console.debug foundDocs["ListItem"]
        console.debug foundDocs["ListItem"][orphanListId]
        bulkDocs.append foundDocs["ListItem"][orphanListId].map (o)->
          o.list_id = newOrphanId
          return o


      console.error "bulkDocs"
      console.log bulkDocs

      console.error "NEW ORPHAN LIST"
      console.log newOrphanList

      
      db.put(newOrphanList)
      .then (response)->
        db.bulkDocs bulkDocs
        .then (response)->
          console.error "bulk update orphans"
          console.debug response




        
      .catch (err)->
        if catchCb?
          catchCb err
        else
          console.log "insert.catch"
          console.log err
      .finally ()->
        console.log "insert finally"
        
        if finallyCb?
          console.log "insert.finally"
          finallyCb()

      

  #//  _                            _   ____        _        
  #// (_)_ __ ___  _ __   ___  _ __| |_|  _ \  __ _| |_ __ _ 
  #// | | '_ ` _ \| '_ \ / _ \| '__| __| | | |/ _` | __/ _` |
  #// | | | | | | | |_) | (_) | |  | |_| |_| | (_| | || (_| |
  #// |_|_| |_| |_| .__/ \___/|_|   \__|____/ \__,_|\__\__,_|
  #//             |_|                                        


  importData = ()->
    console.error "importData"
    console.debug files
    if files?
      console.log "file info"
      fileArray = Array.from(files)
      console.debug fileArray.first()
      reader = new FileReader()
      reader.onloadend = (response)->
        console.log "READ FILE"
        console.log response
        jsonFile  = response.target.result

        jsonToData = JSON.parse(jsonFile).map (o)->
          delete o._rev # clean
          return o


        importedData = jsonToData.groupBy "doctype"

        if Object.has(importedData,"List")
          importedData["List"] = importedData["List"].groupBy("id")
        else
          importedData["List"] = undefined

        if Object.has(importedData,"ListItem")
          importedData["ListItem"] = importedData["ListItem"].groupBy("list_id")
        else
          importedData["ListItem"] = undefined

        console.error "imported data by doctype"
        console.debug importedData

        if Object.has(importedData,"List")
          bulkData = []
          Object.keys(importedData["List"]).forEach (key)->
            # rekey the project
            importedData["List"][key].first()["id"] = uuid()
            importedData["List"][key].first()["_id"] = importedData["List"][key].first()["id"] 
            importedData["List"][key].first()["name"] = "#{importedData["List"][key].first()["name"]} imported at #{Date.create()}"


            bulkData.append importedData["List"][key].first()
            
            console.log "Rekeyed list id #{key} to #{importedData["List"][key]._id}"




            # handle list items for list
            if importedData["ListItem"]?
              if Object.has(importedData["ListItem"],key)
                console.log "found list items"
                console.log "#{importedData["List"][key].name} has #{importedData["ListItem"][key].length} list items"
                # go throug all items

                newKeys = {}


                importedData["ListItem"][key].sortBy (listItem)->
                  return listItem.ancestry.length

                importedData["ListItem"][key].forEach (listItem)->
                  
                  listItem.list_id = importedData["List"][key].first().id # rekey list id to previously created

                  oldId = "#{listItem.id}" # add the current item key to key map

                  listItem["id"] = uuid()
                  listItem["_id"] = listItem["id"] 
                  
                  newKeys[oldId] = listItem.id 
                  
                  listItem.ancestry = listItem.ancestry.map (ancestor)->
                    if newKeys[ancestor]
                      return newKeys[ancestor]
                    else
                      return ancestor
                                    
                console.log "newKeys"
                console.log newKeys

                console.error "finalized importedData"
                console.log importedData
              else
                console.log "#{importedData["List"][key].first().name} has no list items"

              bulkData.append importedData["ListItem"][key]


            console.debug bulkData
            db.bulkDocs(bulkData)
            .then (result)->
              console.error "imported"
              console.debug result          




        #jsonToData.forEach (item)->
          #item.id = uuid()
          #item["_id"] = item.id
          # item.name = "#{item.name} imported at #{Date.create()}"

        
        
        #db.bulkDocs(jsonToData)
        #.then (result)->
        #  console.error "imported"
        #  console.debug result


      reader.readAsText fileArray.first()

  




  notificationPermssionRequest = ()->
    Notification.requestPermission().then (permission) ->
      # If the user accepts, let''s create a notification
      if permission == 'granted'
        notification = new Notification('Hi there!')
      else
        console.log "not granted"
      return

  if electron
    edition = "Desktop"
  else
    edition = "Web"
  


  # resource
 


  compact = ()->
    db.compact()
    .then (result)->
      console.log "COMPACTED"
      console.log result




  


  deleteIndices = ()->

    db.getIndexes().then  (indexesResult) ->
      indexesResult.indexes.forEach (index) =>
        if (index.name != "_all_docs") 
          console.log(index)
          db.deleteIndex(index)



  rawDump = ()->

    # full export
    db.allDocs {
      include_docs: true
      attachments: false
    }
    .then (result)->
      intermediate = result.rows.map (row)->
        return row.doc


      jsonString = JSON.stringify(intermediate,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})

      FileSaver.saveAs blob, "braintapper-radar-dump-#{Date.create().toTimestampId()}.json"
      


  saveAs = ()->

    # full export
    db.allDocs {
      include_docs: true
      attachments: false
    }
    .then (result)->
      intermediate = result.rows.map (row)->
        return row.doc

      console.log "Intermediate items: #{intermediate.length}"
      output = []
      orphanedItems = []
      lists = intermediate.filter (item)->
        delete item._rev
        delete item._id
        return item.doctype == "List"



      lists.sortBy "name"

      console.log "LISTS #{lists.length}"
      console.log lists


      
      
      listItems = intermediate.filter (item)->
        return item.doctype == "ListItem"

      others = intermediate.filter (item)->
        test = false
        if item.doctype?
          if !["List","ListItem"].includes(item.doctype)
            test = true
        else
          if item.id?
            test = true
          
        return test

      orphanedItems.append others


      console.log  "----------- process list items #{listItems.length}"

      console.log "others: #{others.length}"

      console.log listItems
      console.log others
      
      groupedListItems = listItems.groupBy "list_id"

      # console.debug groupedListItems
      
      lists.forEach (list, index)->
        #console.log "LIST"
        #console.log list
        output.append list
        if index < 2
          console.debug output
        if groupedListItems[list.id]?
          output.append groupedListItems[list.id].sortBy (listItem)->
            return listItem.ancestry.join " "
        else
          orphanedItems.append groupedListItems[list.id]

      output.append orphanedItems.map (o)->
        o["list_id"] = "orphan"
        return o



      jsonString = JSON.stringify(output,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})

      FileSaver.saveAs blob, "braintapper-radar-archive-#{Date.create().toTimestampId()}.json"
      

  destroy = ()->

    db.destroy()
    .then (response)->
      console.log "destroyed"
      console.debug response
      window.createPouchDb()
      window.mounted()
    .catch (err)->
      console.error "destroy error"
      console.log err

    ###
    # doesn''t wipe deleted docs from db
    db.allDocs({ include_docs: false })
    .then (response)->
      console.error "DESTROY ALL DOCS"
      docsToBeDeleted = response.rows.map (o)->

        return {
          _id: o.id
          _rev: o.value.rev
          _deleted: true
        }
      console.debug docsToBeDeleted
      db.bulkDocs docsToBeDeleted
      .then (result)->
        console.error "Attempt to delete all docs"
        console.log result
        console.error "COMPACT"
        db.compact()
        .then (result)->
          console.error "compacted"
          console.debug result
    ###

</script>

<svelte:head>
  <title>Settings</title>
</svelte:head>
<PageContent>
  <PageHeader><h1 title>Tools</h1></PageHeader>
  <article type="content">


    <h2>Database</h2>



    <h3>Raw Dump</h3>
    <p>This will save the contents of the entire database.</p>
    <Button on:click={rawDump}>Save Raw Dump to Disk</Button>



    <h3>Export Lists</h3>
    <p>This will save your collections as a JSON file into your downloads folder. This does not export your settings.</p>
    <Button on:click={saveAs}>Save to Disk</Button>

    <h3>Import Data</h3>
    <p>Import data from a JSON file. This will create new IDs for all incoming items, so if you are importing a backup, you may end up with items having duplicate names.</p>
    
    <FileInput bind:files/>

    <Button state="critical" on:click={importData} disabled={false}>Import Data from File</Button>

    <h2>Data</h2>

    <h3>Fix Orphaned List Items</h3>

    <p>This will put all list items whose list cannot be found into a new Orpaned Items List.</p>

    <Button state="critical" on:click={fixOrphans} disabled={false}>Fix Orphans</Button>
    


    <h3>Fix ID Mismatches</h3>

    <p>This will fix items where _id != id</p>

    <Button state="critical" on:click={fixIdMismatch} disabled={false}>Fix ID Mismatches</Button>
    


    <h3>Delete Indices</h3>
    <p>Delete PouchDB indices. This is non-destructive as indices will be recreated as required.</p>
    <Button state="critical" on:click={deleteIndices} disabled={false}>Delete Indices</Button>


    <h3>Danger Zone</h3>

 

    <h2>Delete All Local Data</h2>
    <p>This will delete your local database but not the remote database. It will disable sync settings and delete the remote address of your remote database.</p>
    <Button state="critical" on:click={destroy} disabled={false}>Delete</Button>

   


  </article>

</PageContent>
<style lang="sass">
  article
    margin: 0px

</style>
