<script lang="coffeescript">

  import { createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()



  import { IsDragging, CurrentlyDragging } from "./stores.js"
  
  
  
  import {
    FlexRow
    FlexRowCell
  } from "braintapper-svelte-flex"

  import { 
    debug
    Indent
    IconSvg 
    TippyContextMenu
  } from "../../../Braintapper"
  



  # locally tracked store values

  currentlyDragging = undefined
  isDragging = false



  onMount ()->
    IsDragging.subscribe  (val)->
      isDragging = val
    CurrentlyDragging.subscribe  (val)->
      currentlyDragging = val          




  import Menu from "./_DividerMenu.svelte"


  export index = undefined
  export indentation = 0
  export item = { is_divider: true }
  export listId = undefined
  export firstItem = false

  export delay = 250 # click
  export dragState = false
  export isExpanded = false

  #//      _        _         _                  _    _             
  #//  ___| |_ __ _| |_ ___  | |_ _ __ __ _  ___| | _(_)_ __   __ _ 
  #// / __| __/ _` | __/ _ \ | __| '__/ _` |/ __| |/ / | '_ \ / _` |
  #// \__ \ || (_| | ||  __/ | |_| | | (_| | (__|   <| | | | | (_| |
  #// |___/\__\__,_|\__\___|  \__|_|  \__,_|\___|_|\_\_|_| |_|\__, |
  #//                                                         |___/ 

  isOver = false
  isHover = false
  isInvalid = false

  

  over = (e)->
    e.preventDefault()
    # e.stopPropagation()
    isOver = true

    # isInvalid = false

    # console.error "OVER"

    #if isInvalid
    #  return e.preventDefault()
    #console.log "return is invalid: #{!isInvalid}"
    # return !isInvalid

  hover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      isHover = true
      # console.error "HOVER"

  unhover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      # console.log "UNHOVER"
      isHover = false
      collapse()

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false
    # console.error "LEAVE"
    # isHover = false

  add = (name)->
    console.log "add: index: #{item.index}"
    toIndex = item.index

    isHover = false
    isExpanded = false
    console.error "divider add"
    console.debug { ancestry: item.ancestry, index: toIndex }
    if name?
      dispatch "add", { ancestry: item.ancestry, index: toIndex, name: name }
    else
      dispatch "add", { ancestry: item.ancestry, index: toIndex }




  collapse = (e)->
    # console.log "collapse"
    if e?
      e.stopPropagation()
    isExpanded = false


  # Click tracking

  clickCount = 0

  clickReset = ()->
    switch clickCount
      when 1
        single()
      when 2
        double()
      else
        clickCount = 0
    
  delayedReset = clickReset.debounce(delay)



  single = () ->
    clickCount = 0

    # collapse.delay(5000)
    

    #dispatch "clicked", id

  double = () ->
    clickCount = 0
    # add()
    #dispatch "doubleclicked", id

  click = (e)->
    e.stopPropagation
    clickCount++
    delayedReset()



  ###

  Drop Cases

  1. Drop to Reorder

  2. Drop to Nest and Reorder


  If target ancestry is the same as origin, then it''s a reorder 

  If target ancestry is different from origin, then it''s a nested move

  ###

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false


    eventType = "drop"
  

    ancestry = item.ancestry.clone()

    # ancestry.pop()
    
    #console.log "NestableDivider.drop item"
    #console.debug item
    dropItem = item 
    # { ancestry: ancestry, index: item.index }

    console.log "dropItem"
    console.log dropItem
    console.log e.detail


    #console.log "NestableDivider.svelte"

    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))

    #console.log item

    #console.log "--------------------------------"

    unless firstItem
      if (item.list_items || []).length > 0
        #console.log "------------------------------ DIVIDER DROP"
        #console.log "dragitem"
        #console.debug dragItem
        #console.log "dropitem"
        #console.log item


        #console.log "(item.list_items || []).length #{(item.list_items || []).length} :: nest"
        eventType = "drop"
        item["to_index"] = 0
      else

        console.log "DROP ITEM #{item.name} index: #{item.index}"
        item["to_index"] = item.index + 1

      # console.log "evntType: #{eventType}"

      dropped = true

      unless Object.has(item, "is_divider")
        item["is_divider"] = true

      #console.log "DroppableDivider - drop"
      #console.log item

    else # handle first item
      console.error "FRIST ITEM"
      item["to_index"] = 0

      console.log dragItem
      console.log "eventType #{eventType}"
      dropped = true

    if dropped
      dispatch "drop",
        divider: true
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: dropItem # item
        dropIndex: index + 1
        dropListId: listId


  
  tippyComponent = undefined
  tippyObject = undefined

  menuSelect = (e)->
    switch e.detail
      when "add"
        add()
      when "add-clipboard"
        console.log "added from clipboards"
        navigator.clipboard.readText().then (o)->
          add o



</script>

<div
  divider
  class:over={isOver}
  class:hover={isHover}
  is-dragging={dragState}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
  on:mouseenter={hover}
  on:mouseleave={unhover}
  class:expanded={isExpanded ? "expanded" : false}
  index={item.index}
>
  <FlexRow>
    <FlexRowCell flex="initial">
      <Indent level={indentation} width={32} offset={48}/>
    </FlexRowCell>
    <FlexRowCell flex>
      <TippyContextMenu template={Menu} bind:tippyComponent={tippyComponent} bind:tippyObject={tippyObject} on:select={menuSelect}>
      <div 
        class:expanded={isExpanded ? "expanded" : false}
        class:over={isOver}
        
        class="indented"
      >
    <!--  {item.index} :: ancestry {JSON.stringify(item.ancestry)}  -->
      
      
        <div class="droptarget"
          class:invalid={isInvalid}>
            &nbsp;

            &nbsp;
        </div>
        </div>
      </TippyContextMenu>
    </FlexRowCell>

   
  </FlexRow>


</div>


<style lang="sass">



  [divider]

    --item-draggable-icon-width: 24px 
    // margin: 2px 0px
    height: 8px
    // debug
    //background: var(--lightest-red)
    // height: 24px
    //transition: height 0.15s ease-out


    // match footprint to draggable container
    width: calc(100% + calc(3 * var(--item-draggable-icon-width)))
    max-width: calc(100% + calc(3 * var(--item-draggable-icon-width)))
    // 2 icons grabber, expander
    margin-left: calc(var(--item-draggable-icon-width) * -2)



    border-radius: 3px

    .indented
      border-radius: 4px
      background: transparent


    &:hover, &.over
      background: var(--lightest-blue)
      .indented
        background: var(--light-blue)//var(--list-row-highlight)
    
    //&.over
    //  height: 24px
    //  transition: height 0.15s ease-in
    //  .indented
    //    background: #f1f1f1
    
    &.expanded
      height: 40px
      //transition: height 0.15s ease-in


    .expanded
      padding: 8px 0px
      border-top: 8px solid transparent
      border-bottom: 8px solid transparent

      height: 40px

      .indented
        background: transparent
    //background: var(--lightest-blue)


    // drag and drop
    .droptarget
      height: 8px
      color: transparent
      // line-height: 24px

    .indented.over
      .droptarget
        padding: 0px 8px
        color: var(--light-gray) !important
        background: transparent
        border-radius: 4px




    .add-divider
      text-align: left
      border-radius: 3px
      border: 0px
      padding: 0px 8px
      //line-height: 24px
      top: -6px
      vertical-align: middle
      button
        //line-height: 24px
        margin-right: 8px
      [icon]
        padding-right: 8px
        svg
          fill: var(--green)
        color: var(--green)
      button[cancel]
        margin-left: 18px     
        svg
          fill: var(--red)
        color: var(--red)           
      // background: var(--green) !important
    // hover state


    &[is-dragging="true"] //.dragging
      //height: 24px
      //transition: height 0.15s ease-in      
      //margin: 2px 0px
      
      //.indented
      //  background: #f1f1f1      
      // height: 24px
      // transition: height 0.15s ease-in
      // margin-bottom: 2px
      .indented
        background: #f1f1f1
      .droptarget
        padding: 0px 8px 
        color: var(--light-gray) !important
        background: transparent
        border-radius: 4px
        svg
          path
            fill: var(--light-gray)
      &.over
        .indented
          background: var(--gray)
          color: white



    &.invalid
      .placeholder
        background: var(--red) !important




    div[spacer] // accommodate expand collapse icon
      width: 24px

</style>
